.SearchBox {
  width: 100%;
  /* height: 330px; */
  /* height: 450px; */
  height: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* margin-bottom: 35px; */
}
.SearchBox .searchBar-icon {
  position: relative;
  display: flex;
  justify-content: center;
  width: 510px;
  margin-left: 10px;
}
img.searchIcon {
  width: 30px;
  cursor: pointer;
  position: absolute;
  left: 440px;
  transform: translateY(-65%);
  /* left: 375px; */
  top: 33px;
  z-index: 10;
}

.SearchBox select,
input,
.diet {
  margin: 5px;
  border-radius: 30px;
  width: 200px;
  height: 30px;
  border-style: none;
  box-sizing: border-box;
  padding: 5px 10px;
  font-size: 14px;
  color: #76797a;
  border: 2px solid #85bfb2;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  /* background-color: #e2fef7; */
}
/* input {
  width: 500px;
} */

.SearchBox .drop-down-container {
  display: flex;
  justify-content: center;
  width: 330px;
  /* margin-bottom: 100px; */
  margin-left: 15px;
}

.SearchBox #SearchBar {
  width: 100%;
  height: 50px;
  position: relative;
  padding: 25px;
  margin: 20px;
  margin-top: 0px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 30px;
  font-size: 20px;
  /* color: white; */
}

.SearchBox ::placeholder {
  /* color: white; */
  font-weight: 200;
}

.SearchBox select {
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  appearance: none;
}

.SearchBox .fa-angle-down {
  position: relative;
  left: 60px;
  /* left: 175px; */
  bottom: 30px;
  color: #ad2632;
  pointer-events: none;
}
#calories {
  margin: 5px;
  border-radius: 30px;
  width: 150px;
  height: 30px;
  border: 2px solid #85bfb2;
}
.searchResults {
  /* flex-wrap: wrap; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.searchResults img {
  /* margin-left: 50px; */
  width: 380px;
  height: 281px;
  padding: 10px;
}

@media only screen and (min-width: 768px) {
  .searchResults {
    display: flex;
    flex-direction: row;
  }
}
